import * as React from 'react'
import Waterfall from 'src/packages/waterfall/Waterfall'
import Code from 'src/components/Code'
import CodeBox from 'src/components/CodeBox'
import Space from 'src/components/Space'
import { renderString } from 'src/utils/renderCode'

const componentName = 'WaterfallExample'

class WaterfallExample extends React.Component {
  public static displayName = componentName

  public render() {
    return (
      <div className="waterfall-example">
        <section>
          <h1>Waterfall 瀑布流</h1>
          <p className="text">将页面元素固定在可视范围。</p>
        </section>
        <section>
          <h2>何时使用</h2>
          <p className="text">
            当内容区域出现滚动，需要部分内容始终在可视范围内展示时。
          </p>
        </section>
        <section>
          <h2>代码示例</h2>
        </section>
        <div className="example">
          <div className="container">
            <Waterfall>
              {Array.from({ length: 30 }, (val, index) => index + 1).map(
                num => (
                  <div
                    key={`id-${num}`}
                    style={{
                      height: `${((num % 5) + 1) * 25}px`,
                      border: '1px solid rgba(0,0,0,0.15)',
                      display: 'flex',
                      justifyContent: 'center',
                      alignItems: 'center'
                    }}
                  >
                    {num}
                  </div>
                )
              )}
            </Waterfall>
          </div>
          <CodeBox title="基本" description="基本的用法。">
            {renderString(`import { Affix } from 'xue-react'`)}
            {renderString(`import 'xue-react/lib/xue-react.css'`)}
            <br />
            <Code code={`&lt;Affix distance={20}&gt;`} />
            <Space count={4} />
            <span className="label">&lt;</span>
            <span className="htmltag">div</span>
            <span className="label">&gt;</span>
            <span className="htmlstr">Affix</span>
            <span className="label">&lt;</span>
            <span className="label">/</span>
            <span className="htmltag">div</span>
            <span className="label">&gt;</span>
            <br />
            <Code code={`&lt;/Affix&gt;`} />
          </CodeBox>
        </div>
        <section>
          <h2>API</h2>
        </section>
        <table className="api-table">
          <thead>
            <tr>
              <th>参数</th>
              <th className="des">说明</th>
              <th className="type">类型</th>
              <th>默认值</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>distance</td>
              <td className="des">距离窗口顶部达到指定偏移量后触发</td>
              <td className="type">number</td>
              <td>0</td>
            </tr>
            <tr>
              <td>className</td>
              <td className="des">自定义固钉容器类名</td>
              <td className="type">string</td>
              <td>——</td>
            </tr>
            <tr>
              <td>style</td>
              <td className="des">自定义固钉容器样式</td>
              <td className="type">React.CSSProperties</td>
              <td>——</td>
            </tr>
          </tbody>
        </table>
      </div>
    )
  }
}

export default WaterfallExample



