import * as React from 'react'
import Spin from 'src/packages/spin/Spin'
import Code from 'src/components/Code'
import CodeBox from 'src/components/CodeBox'
import Space from 'src/components/Space'
import { renderString } from 'src/utils/renderCode'
import Button from 'src/packages/button/Button'

const componentName = 'SpinExample'

class SpinExample extends React.Component {
  public static displayName = componentName

  public state = {
    spinning1: true,
    spinning2: true
  }

  public render() {
    return (
      <div className="spin-example">
        <section>
          <h1>Spin 加载中</h1>
          <p className="text">用于页面和区块的加载中状态。</p>
        </section>
        <section>
          <h2>何时使用</h2>
          <p className="text">
            页面局部处于等待异步数据或正在渲染过程时，合适的加载动效会有效缓解用户的焦虑。
          </p>
        </section>
        <section>
          <h2>代码示例</h2>
        </section>
        <div className="example">
          <div className="container">
            <Spin />
          </div>
          <CodeBox title="基本用法" description="一个简单的 loading 状态。">
            {renderString(`import Spin from 'xue-react'`)}
            <Code code={`&lt;Spin /&gt;`} />
          </CodeBox>
        </div>
        <div className="example">
          <div className="container">
            <Spin tip="loading" />
          </div>
          <CodeBox title="自定义描述文案" description="自定义描述文案。">
            {renderString(`import Spin from 'xue-react'`)}
            <Code code={`&lt;Spin tip="loading" /&gt;`} />
          </CodeBox>
        </div>
        <div className="example">
          <div className="container">
            <Button
              onClick={() =>
                this.setState({ spinning1: !this.state.spinning1 })
              }
              type="primary"
              style={{ margin: '10px' }}
            >
              Toggle
            </Button>
            <Spin spinning={this.state.spinning1} />
          </div>
          <CodeBox
            title="受控"
            description="通过 spinning 属性控制 loading 状态。"
          >
            {renderString(`import Spin from 'xue-react'`)}
            <Code code={`&lt;Spin spinning={this.state.spinning1} /&gt;`} />
          </CodeBox>
        </div>
        <div className="example">
          <div className="container" style={{ flexDirection: 'column' }}>
            <Spin spinning={this.state.spinning2} tip="loading..." size={30}>
              <div
                style={{
                  padding: '20px'
                }}
              >
                Further details about the context of this element.
              </div>
            </Spin>
            <Button
              onClick={() =>
                this.setState({ spinning2: !this.state.spinning2 })
              }
              type="primary"
              style={{ marginTop: '20px' }}
            >
              Toggle
            </Button>
          </div>
          <CodeBox
            title="容器加载中"
            description="可以直接把内容内嵌到 Spin 中，将现有容器变为加载状态。"
          >
            {renderString(`import Spin from 'xue-react'`)}
            <Code
              code={`&lt;Spin spinning={this.state.spinning2} tip="loading..." size={30}&gt;`}
            />
            <Space count={4} />
            <span className="label">&lt;</span>
            <span className="htmltag">div</span>
            <span className="label">&gt;</span>
            <span className="htmlstr">
              Further details about the context of this element.
            </span>
            <span className="label">&lt;</span>
            <span className="label">/</span>
            <span className="htmltag">div</span>
            <span className="label">&gt;</span>
            <br />
            <Code code={`&lt;/Spin&gt;`} />
          </CodeBox>
        </div>
        <section>
          <h2>API</h2>
        </section>
        <table className="api-table">
          <thead>
            <tr>
              <th>参数</th>
              <th className="des">说明</th>
              <th className="type">类型</th>
              <th>默认值</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>spinning</td>
              <td className="des">是否为加载中状态</td>
              <td className="type">boolean</td>
              <td>true</td>
            </tr>
            <tr>
              <td>size</td>
              <td className="des">加载图标的大小，单位：px</td>
              <td className="type">number</td>
              <td>16</td>
            </tr>
            <tr>
              <td>tip</td>
              <td className="des">自定义描述文案</td>
              <td className="type">string</td>
              <td>——</td>
            </tr>
            <tr>
              <td>className</td>
              <td className="des">自定义 Spin 容器类名</td>
              <td className="type">string</td>
              <td>——</td>
            </tr>
            <tr>
              <td>style</td>
              <td className="des">自定义 Spin 容器样式</td>
              <td className="type">React.CSSProperties</td>
              <td>——</td>
            </tr>
          </tbody>
        </table>
      </div>
    )
  }
}

export default SpinExample



